.front_blog {
  padding: 100px 0;
  background-color: $c-primary-100;
  @media (max-width: 1366px) {
    padding: 90px 0;
  }
  @media (max-width: 1280px) {
    padding: 80px 0;
  }
  @media (max-width: 1024px) {
    padding: 70px 0;
  }
  @media (max-width: 768px) {
    padding: 60px 0;
  }
  @media (max-width: 576px) {
    padding: 50px 0;
  }
  .container {
    h2 {
      margin: 0;
      color: $c-white;
      text-align: center;
    }
    .subtitle {
      @extend .trans-all-2;
      margin-bottom: 50px;
      @media (max-width: 1366px) {
        margin-bottom: 44px;
      }
      @media (max-width: 1280px) {
        margin-bottom: 38px;
      }
      @media (max-width: 1024px) {
        margin-bottom: 32px;
      }
      @media (max-width: 768px) {
        margin-bottom: 26px;
      }
      @media (max-width: 576px) {
        margin-bottom: 20px;
      }
      a {
        display: flex;
        gap: 6px;
        align-items: center;
        justify-content: center;
        span {
          color: $c-white;
          font-size: 14px;
        }
        &:hover {
          gap: 10px;
          opacity: 0.8;
        }
      }
    }
    .blog_items {
      margin: 0 11.6%;
      @media (max-width: 1024px) {
        margin: 0;
      }
      article {
        figure {
          @extend .trans-all-5;
          margin-bottom: 20px;
          @media (max-width: 1366px) {
            margin-bottom: 18px;
          }
          @media (max-width: 1280px) {
            margin-bottom: 16px;
          }
          @media (max-width: 1024px) {
            margin-bottom: 14px;
          }
          @media (max-width: 768px) {
            margin-bottom: 12px;
          }
          @media (max-width: 576px) {
            margin-bottom: 10px;
          }
          a {
            display: block;
            border-radius: var(--br-lg);
            overflow: hidden;
            img {
              width: 100%;
            }
            &:hover {
              img {
                transform: matrix(1.04, 0, 0, 1.04, 0, 5);
              }
            }
          }
        }
        h3 {
          margin: 0 8%;
          color: $c-white;
          font-size: 30px;
          font-weight: 400;
          text-align: center;
          @media (max-width: 1366px) {
            font-size: 28px;
          }
          @media (max-width: 1280px) {
            font-size: 26px;
          }
          @media (max-width: 1024px) {
            margin: 0;
            font-size: 24px;
          }
          @media (max-width: 768px) {
            font-size: 22px;
          }
          @media (max-width: 576px) {
            font-size: 18px;
          }
        }
      }
    }
  }
}
